.news {
    color: #fff;
    margin-bottom: 110px;
    overflow: hidden;
    @include md {
        margin-bottom: 72px; }
    @include sm {
        margin-bottom: 75px; }
    &__content {
        position: relative;
        .box_blurred {
            top: 200px;
            left: 890px; } }
    &__title {
        margin-bottom: 33px;
        @include md {
            margin-bottom: 25px; } }
    &__body {
        text-align: center;
        @include md {
            padding: 0 7px;
            .button_small {
                padding: 14px 63px; } } }
    &__list {
        display: flex;
        justify-content: space-between;
        // display: grid
        // grid-template-columns: repeat(3, 1fr)
        // grid-column-gap: 35px
        text-align: left;
        margin-bottom: 35px;
        @include md {
            margin-bottom: 10px; }
        @include sm {
            flex-wrap: wrap;
            margin-bottom: 40px; } }
    &__item {
        margin-right: 35px;
        width: 33.3%;
        @include md {
            margin-right: 24px; }
        @include sm {
            width: 100%;
            margin-right: 0;
            margin-bottom: 45px;
            &:last-child {
                margin-bottom: 0; } }
        &:last-child {
            margin-right: 0; } } }

.item {
    &__img {
        margin-bottom: 18px;
        width: 100%;
        overflow: hidden;
        border-radius: 8px;
        @include md {
            margin-bottom: 25px; }
        img {
            display: block;
            width: 100%; } }
    &__text {}
    &__title {
        margin-bottom: 13px;
        font-size: 30px;
        font-weight: 600;
        @include md {
            font-size: 24px;
            line-height: 1.25; }
        @include sm {
            margin-bottom: 20px; } }
    &__content {
        font-size: 14px;
        font-weight: 400;
        line-height: 1.57;
        @include md {
            line-height: 1.58; } } }
