.forecasts {
	position: relative;
	padding-bottom: 147px;
	// background-image: linear-gradient(30.05deg,#1d354e 50.2%,transparent 0)
	@include md {
		padding-bottom: 57px; }
	@include sm {
		padding-bottom: 27px; }
	&__content {
		position: relative;
		display: flex;
		flex-direction: column;
		padding-top: 84px;
		align-items: flex-end;
		@include md {
			padding-top: 52px; }
		@include sm {
			padding-top: 67px;
			align-items: center; } }
	&__text {
		margin: 0 90px 210px 0;
		font-weight: 600;
		color: #fff;
		text-align: center;
		// float: right
		z-index: 10;
		@include md {
			margin: 0 33px 166px 0; }
		@include sm {
			margin: 0 5px 310px;
			text-align: center; }
		h3 {
			font-size: 72px;
			margin-bottom: 10px;
			font-weight: 600;
			@include md {
				font-size: 48px;
				line-height: 1;
				margin-bottom: 8px; }
			@include sm {
				font-size: 36px; } } }
	&__subtitle {
		font-size: 30px;
		margin-bottom: 25px;
		line-height: 1.5;
		@include md {
			font-size: 18px;
			line-height: 1.7;
			margin-bottom: 15px; }
		@include sm {
			margin-bottom: 30px; } }

	.box_turquoise {
		top: 248px;
		left: -243px;
		@include md {
			top: 136px;
			left: -91px; }
		@include sm {
			display: none; } }
	.box_blurred {
		z-index: 5;
		top: 380px;
		right: -165px;
		@include md {
			top: 220px;
			right: -115px; }
		@include sm {
			display: none; } }
	&_bg {
		position: absolute;
		top: 664px;
		left: -806px;
		width: 2500px;
		height: 2500px;
		transform: rotate(30.05deg);
		background-color: $main_color;
		z-index: -5;
		@include md {
			width: 1500px;
			height: 1500px;
			top: 592px;
			left: -486px; }
		@include sm {
			top: auto;
			bottom: -1451px;
			left: -569px; } }
	// 	// -webkit-clip-path: polygon(0 13.5%, 100% 86.5%, 100% 100%, 0 100%)
	// 	// clip-path: polygon(0 13.5%, 100% 86.5%, 100% 100%, 0 100%)
 }	// 	z-index: -5

.forecasts__video {
	position: relative;
	width: 100%;
	// z-index: -1

	.robot {
		position: absolute;
		top: -759px;
		left: -80px;
		width: 1102px;
		@include md {
			top: -564px;
			left: -58px;
			width: 820px; }
		@include sm {
			width: 505px;
			top: -431px;
			left: -42px; }
		// z-index: -1
		img {
			display: block;
			width: 100%; }
		// &__eyeLight
		// 	position: absolute
		// 	top: -185px
 } }		// 	left: 355px
.video {
	position: relative;
	padding: 48px;
	width: 100%;
	overflow: hidden;
	background-color: #fff;
	border-radius: 16px;
	@include md {
		padding: 24px; }
	@include sm {
		padding: 10px;
		border-radius: 8px; }
	// z-index: -2
	video {
		display: block;
		width: 100%;
		height: 100%;
		border-radius: 8px;
		@include sm {
			border-radius: 2px; } }
	&__play {}
	&__wrapper {
		position: relative;
		width: 120px;
		height: 120px;
		@include sm {
			width: 60px;
			height: 60px; }
		img {
			width: 100%; } } }

.video__play,
.video__button {
	@include vcenter;
	z-index: 10;
	cursor: pointer; }
.video__button {
	margin-left: 18px;
	border: 24px solid transparent;
	border-left: 36px solid #fff;
	@include sm {
		margin-left: 9px;
		border: 10px solid transparent;
		border-left: 18px solid #fff; } }

