.footer {
    position: relative;
    padding-bottom: 72px;
    @include md {
        .container-small {
            padding: 0 72px; } }
    @include sm {
        padding-bottom: 40px;
        .container-small {
            padding: 0 13px; } }
    &__content {
        position: relative;
        display: flex;
        justify-content: space-between;
        align-items: center;
        @include sm {
            flex-direction: column;
            align-items: flex-start; } }

    &__nav {
        ul {
            display: flex;
            color: #fff;
            font-size: 18px;
            li {
                margin-right: 40px;
                &:last-child {
                    margin-right: 0; } } } }
    .box_medium {
        bottom: 238px;
        left: -371px;
        @include md {
            left: 270px;
            bottom: -175px;
            z-index: -1; }
        @include sm {
            left: 186px;
            bottom: 57px; } }
    .slanting-bg {
        position: absolute;
        top: 50%;
        left: 50%;
        width: 300vw;
        height: 500%;
        margin: 0 0 0 -723px;
        transform: translate(-50%, -50%) rotate(30.05deg);
        background-color: $main_color;
        z-index: -5;
        @include md {
            margin-left: -522px; }
        @include sm {
            margin-left: 0;
            margin-top: 235px; } } }

.social {
    @include sm {
        margin-bottom: 30px; }
    ul {
        display: flex;
        li {
            margin-right: 11px;
            &:last-child {
                margin-right: 0; } } }
    .icon {
        font-size: 37px; } }
