.subscribe {
    position: relative;
    padding-top: 166px;
    margin-bottom: 50px;
    @include md {
        padding-top: 108px;
        margin-bottom: 66px;
        .container-small {
            padding: 0 72px; } }
    @include sm {
        padding-top: 65px;
        margin-bottom: 200px;
        .container-small {
            padding: 0 10px; } }
    &__content {
        position: relative;
        display: flex;
        justify-content: space-between;
        @include sm {
            flex-direction: column; } }
    &__social {
        padding: 70px 0 0 2px;
        text-align: center;
        @include md {
            padding-top: 28px;
            margin-right: 30px; }
        @include sm {
            padding-top: 58px;
            margin-right: 0; }
        @include sm {
            order: 2; } }
    &__telegram {
        margin-bottom: 31px;
        li {
            margin-bottom: 24px;
            padding: 21px 89px;
            font-size: 18px;
            background-color: #fff;
            border-radius: 32px;
            @include md {
                padding: 21px 30px; }
            &:last-child {
                margin-bottom: 0; }
            .icon {
                font-size: 22px;
                margin-right: 10px; }
            a {
                display: flex;
                align-items: center;
                justify-content: center; } } }
    &__email {
        font-size: 18px;
        color: #fff; }
    &__form {
        display: flex;
        flex-direction: column;
        width: 500px;
        padding: 38px 48px 53px;
        border: 16px solid #56ecf6;
        border-radius: 8px;
        @include md {
            width: 379px;
            padding: 25px 20px 42px;
            border: 12px solid #56ecf6; }
        @include sm {
            order: 1;
            width: 100%;
            padding: 30px 26px 40px; }
        .subscribe__title {
            margin-bottom: 21px;
            @include md {
                margin-bottom: 28px; }
            @include sm {
                margin-bottom: 17px; }
            span {
                line-height: 0.8; } }
        input {
            width: 100%;
            margin-bottom: 44px;
            padding-left: 10px;
            font-size: 24px;
            line-height: 65px;
            font-family: $main_font;
            font-weight: 600;
            border: none;
            background: none;
            color: #fff;
            @include placeholder-color(#fff);
            border-bottom: 2px solid #56ecf6;
            @include md {
                font-size: 18px;
                line-height: 56px;
                margin-bottom: 46px; }
            @include sm {
                font-size: 16px;
                line-height: 50px;
                padding-left: 20px;
                margin-bottom: 23px; } }
        .button_small {
            @include md {
                padding: 21px 48px;
                border-radius: 32px;
                font-size: 18px; }
            @include sm {
                border-radius: 32px; } } }
    .box_turquoise {
        top: -61px;
        right: -315px;
        @include md {
            top: -89px;
            right: -26px; }
        @include sm {
            top: -55px;
            right: -70px; } } }
