.questions {
    &__content {
        position: relative;
        padding: 80px 100px 93px;
        background-color: #fff;
        border-radius: 16px;
        @include md {
            padding: 40px 33px 17px; }
        @include sm {
            padding: 25px 30px 11px;
            border-radius: 8px; }
        .box {
            top: 89px;
            left: -90px;
            @include md {
                top: -57px;
                left: auto;
                right: 62px; }
            @include sm {
                top: -94px;
                left: -67px;
                right: auto; } } }
    &__title {
        margin-bottom: 53px;
        font-size: 72px;
        line-height: 1;
        @include md {
            margin-bottom: 34px;
            font-size: 48px; }
        @include sm {
            font-size: 36px;
            margin-bottom: 28px; } }
    &__list {}
    &__item {
        position: relative;
        padding-left: 55px;
        margin-bottom: 25px;
        @include md {
            margin-bottom: 34px;
            padding-left: 67px; }
        @include sm {
            padding-left: 47px;
            margin-bottom: 22px; }
        &:hover {
            .questions__img {
                transform: rotate(90deg); }
            .questions__subtext {
                position: relative;
                transform: translateY(0);
                opacity: 1; } } }

    &__img {
        position: absolute;
        top: 6px;
        left: 2px;
        padding-left: 4px;
        width: 36px;
        height: 36px;
        border-radius: 50%;
        background-color: $main_bg;
        text-align: center;
        line-height: 41px;
        @include trans(all, .5s, ease);
        @include md {
            top: -3px; }
        @include sm {
            width: 27px;
            height: 27px;
            left: 4px;
            padding-left: 2px;
            line-height: 31px;
            img {
                width: 7px;
                height: 12px; } }
        // &:before
        //     content: "\203A"
        //     position: absolute
        //     top: 6px
        //     left: 2px
        //     width: 36px
        //     height: 36px
        //     font-size: 40px
        //     line-height: 30px
        //     border-radius: 50%
        //     background-color: #00bdca
        //     text-align: center
        //     color: #fff
 }        //     cursor: pointer
    &__text {
        margin-bottom: 10px;
        font-size: 30px;
        line-height: 1.6;
        @include md {
            font-size: 24px;
            line-height: 1.25;
            margin-bottom: 14px; }
        @include sm {
            font-size: 18px;
            line-height: 1.35; } }
    &__subtext {
        position: absolute;
        opacity: 0;
        font-size: 18px;
        line-height: 2;
        font-weight: 300;
        transform: translateY(-110%);
        @include trans(all, .7s, ease);
        @include md {
            font-size: 14px;
            line-height: 1.6; } } }
